@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
* {
  font-family: 'Nunito'
}
body {
  margin: 0;
  font-family: 'Nunito', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  width: 100vw;
  min-height: 100vh;
  background-color: #090D28;
  color: #fff;
}

.app__container {
  max-width: 500px;
  margin: 0 auto;
  padding: 20px;
  background: url(/selendra-faucet/static/media/950.ee91927c.png) no-repeat;
  border-radius: 8px;
}

.app__title {
  font-size: 32px;
  font-weight: 600;
}

.app__container Input {
  border-radius: 8px;
  width: 100%;
  height: 48px;
  background: #f5f5f5!important;
  color: #090D28!important;
  border-color: #585D86;
}

.ant-form-item-label > label {
  color: #fff!important;
}

.app__container Button {
  height: 44px;
  border: 0;
  border-radius: 8px;
  background: #08B952!important;
  color: #fff;
  font-weight: 600;
}

.app__container Button:hover {
  color: #fff;
  background: #069943!important;
}
.app__card {
  border-radius: 8px;
  padding: 10px 20px;
  margin-bottom: 10px;
  background: #181C35;
}

.app__return {
  text-align: center;
  font-size: 24px;
  font-weight: 600;
}

.app__address {
  font-size: 18px;
  font-weight: 600;
}

#receive__wallet {
  position: absolute;
  color: #fff;
  opacity: 0;
  width: 1px;
  height: 1px;
  font-size: 1px;
  left: 0;
  box-sizing: content-box;
  -webkit-user-select: initial;
  outline: none;
  margin-left: -9999px;
}

.app__copy {
  cursor: pointer;
  transition: .6s;
}

.app__copy:hover {
  color: #08B952;
}

/* SmartPhone */
@media only screen and (max-width: 500px) {
  .App {
    padding: 0 10px;
  }
}
.header {
  width: 100vw;
  height: 100px;
  padding: 10px 0;
  background-color: #090D28;
}

.header__logo {
  width: 44px;
  height: 66px;
}
